.page-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 41px auto 35px;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    padding: 33px 20px 20px;
    margin: 0;
  }

  @media (min-width: @desktop-width) {
    min-height: 197px;
    width: 940px;
    margin: 0 auto;
    padding: 20px 0 25px;
  }
}

.page-footer__logo-mobile {
  fill: @black-green;
  color: @cherry;

  @media (min-width: @desktop-width) {
    display: none;
  }
}

.page-footer__logo-desktop {
  display: none;

  @media (min-width: @desktop-width) {
    display: block;
    color: @cherry;
    fill: @black-green;
  }
}

.page-footer__link-logo {
  &:hover .page-footer__logo-desktop {
    fill: @cherry;
  }

  &:active .page-footer__logo-desktop {
    fill: @black-green;
    opacity: 0.3;
  }

  &:hover .page-footer__logo-mobile {
    fill: @cherry;
  }

  &:active .page-footer__logo-mobile {
    fill: @black-green;
    opacity: 0.3;
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 15px;
  }
}

.page-footer__list {
  list-style: none;
  padding: 0;
  margin: 35px 0 33px;
  max-width: 232px;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  gap: 5px;

  @media (min-width: @tablet-width) {
    margin: 0;
    margin-right: 15px;
    max-width: 291px;
    gap: 8px;
  }

  @media (min-width: @desktop-width) {
    margin: 0;
    margin-left: 7px;
    max-width: 586px;
  }
}

.page-footer__text {
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  color: @black-green;

  @media (min-width: @desktop-width) {
    font-size: 18px;
  }
}

.page-footer__logo-academy {
  fill: @cherry;
}

.page-footer__copyright {
  text-decoration: none;
  color: inherit;
  width: 129px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &:hover .page-footer__logo-academy {
    fill: @black-green;
  }

  &:active .page-footer__logo-academy {
    fill: @black-green-03;
  }

  @media (min-width: @desktop-width) {
    width: 153px;
  }
}
